@mixin bp($size) {
	@media (min-width: $size) {
		@content;
	}
}

@mixin bpmax($size) {
	@media (max-width: $size) {
		@content;
	}
}

@mixin retina() {
	@media (min--moz-device-pixel-ratio: 1.3),
	(-o-min-device-pixel-ratio: 2.6/2),
	(-webkit-min-device-pixel-ratio: 1.3),
	(min-device-pixel-ratio: 1.3),
	(min-resolution: 1.3dppx) {
		@content;
	}
}

@mixin z($index) {
	z-index: map-get($z, $index);
}

@mixin fullBg {
	background: 50% no-repeat;
	background-size: cover;
}

@mixin cols($gutterTop:60px, $gutterLeft:60px, $colums:3) {
	margin: (-$gutterTop) 0 0 (-$gutterLeft);

	@include bp($l) {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
	}

	.col {
		padding: $gutterTop 0 0 $gutterLeft;

		@include bp($l) {
			max-width: percentage(1/$colums);
		}
	}
}

@mixin flex-valign($valign) {
	align-items: $valign;
	display: flex;
	height: 100%;
}

@mixin rgba-background($hexcolor, $opacity) {
  background-color: $hexcolor;
  background-color: rgba($hexcolor, $opacity); 
}

@mixin objectFit($position: default) {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;

	@if $position == default {
		object-position: center center;
	} @else if $position == left {
		object-position: left center;
	} @else if $position == right {
		object-position: right center;
	}
 	@else if $position == top {
		object-position: center top;
	}
}

@mixin mobile-menu() {
	flex-direction: column;
	margin-top: 60px;

	li {
		padding-right: 0;
		margin-bottom: 32px;
		transform: translateX(30px);
		&:first-child {
			order: 10;
			margin-bottom: 0;
			margin-top: 70px;
			transition-delay: .63s !important; 
		}

		&.image-link {
			.img {
				display: none;
				&-mobile {
					display: block;
				}
			}
		}

		.button {
			margin-top: 30px;
		}

		a {
			&:not(.button) {
				font-size: 22px;
				font-weight: bold;
				// &:after {
				// 	display: none;
				// }
			}
		}
	}

	@for $i from 1 through 10 {
		li:nth-child(#{$i}n) {
			transition-delay: ($i * .09 + s);
		}
	}

}

@mixin scrolled-menu() {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	max-width: 490px;
	opacity: 0;
	overflow: visible;
	padding: 80px 20px 60px 20px;
	position: fixed;
	top: 0;
	width: 530px;
	right: -530px;
	transition: all $speed3 $easing-cubic;
	z-index: 10000;

	&:after {
		background-color: $black;
		background-image: url(../../images/bg/texture.webp);
		background-position: top center;
		background-repeat: repeat-y;
		content: '';
		display: block;
		min-height: 100vh;
		position: absolute;
		top: 0;
		width: 100%;
		z-index: -1;
	}

	.logo-lg {
		display: block;
	}	
}








