@import "../../assets/css/parts/variables";
@import "../../assets/css/parts/mixins";

.sec--gallery {
    margin-top: 75px;
    margin-bottom: 65px;

    .lg-wrapper {
        display: block;
        min-height: 100%;
        min-width: 100%;
        height: 100%;
        width: 100%;
    }

    .container {

        &-filter {
            a {
                margin-right: 20px;
                &:after {
                    transform: scale(1);
                    width: calc(100% + 10px);
                    left: -5px;
                }
                &:last-child {
                    margin-right: 0;
                }
                &:hover,
                &.active {
                    &:after {
                        height: 120%;
                        left: -5px;
                        width: calc(100% + 10px);
                    }
                }
            }
        }

        &-masonry {
            flex-direction: column;
            margin-top: 45px;
            max-width: 1600px;
        }

    }

    .wrapper {

        &-gallery-item {
            &-hide {
                display: none !important;
            }
        }

        &-gallery-item,
        &-gallery-item-nested {

            margin-bottom: 18px;
            position: relative;
            width: 100%;
            overflow: hidden;

            a {
                cursor: url(../../../assets/images/arrows/cursor-open.png) 40 40, pointer !important;
                position: relative;
                &:after {
                    background-image: linear-gradient(165deg, rgba(216, 190, 142, 0) 5%, darken(#d8be8e, 10%) 100%);
                    content: '';
                    display: block;
                    height: 100%;
                    opacity: 0;
                    position: absolute;
                    pointer-events: none;
                    top: 0;
                    width: 100%;
                    z-index: 0;
                    transition: all $speed $easing;
                }

                &:hover {
                    
                    &:after {
                        opacity: 1;
                    }
                }
            }

            &:hover {
                
                > .wrapper-meta {
                    opacity: 1;
                    transform: translateX(0); 
                    &:after {
                        transform: translate3d(0);
                    }
                }

                > .overlay {
                    transform: scale(1) translate(-85%, -55%);
                    opacity: 1;
                }
            }

        }

        &-gallery-item,
        &-gallery-item-nested {

            &.layout-1,
            &.layout-9,
            &.layout-12,
            &.layout-20 {
                min-height: 315px;
                &:before {
                    padding-bottom: 56.25%;
                }
                a {
                    min-height: 315px;
                }
            }

            &.layout-2,
            &.layout-3,
            &.layout-5,
            &.layout-7,
            &.layout-10,
            &.layout-11,
            &.layout-14,
            &.layout-15,
            &.layout-17,
            &.layout-19 {
                min-height: 373px;
                max-height: 373px;
                &:before {
                    padding-bottom: 66.6071%;
                }
                a {
                    min-height: 373px;
                    max-height: 373px;
                }
            }

            &.layout-4,
            &.layout-13 {
                min-height: 204px;
                max-height: 204px;
                .wrapper-gallery-item-nested {
                    &:before {
                        padding-bottom: 75%;
                    }
                }
                a {
                    min-height: 204px;
                    max-height: 204px;
                }
            }

            &.layout-6,
            &.layout-16 {
                min-height: 362px;

                .wrapper-gallery-item-nested {
                    max-height: 362px;
                    &:before {
                        padding-bottom: 75.1381%;
                    }
                }
            }

            &.layout-4,
            &.layout-6,
            &.layout-13,
            &.layout-16 {
                .wrapper-gallery-item-nested {

                    &:first-child {
                        margin-right: 16px;
                        order: 1 !important;
                    }
                    &:last-child {
                        margin-right: 0;
                        order: 2 !important;
                    }
                }                
            }

            &.layout-8,
            &.layout-18 {
                min-height: 230px;
                max-height: 230px;
                &:before {
                    padding-bottom: 50%;
                }
                a {
                    min-height: 230px;
                    max-height: 230px;
                }
            }

            &.wrapper-group {
                display: flex;
                flex-direction: row;
                width: 100%;
                &:before {
                    display: none;
                }

                .wrapper-gallery-item-nested {
                    flex-basis: calc(50% - 8px) !important;
                    margin-bottom: 0;
                    width: 100%;

                    &:first-child {
                        margin-right: 16px;
                        order: 1 !important;
                    }
                    &:last-child {
                        margin-right: 0;
                        order: 2 !important;
                    }

                    .wrapper-gallery-item-nested {
                        min-width: calc(50% - 8px) !important;
                    }

                }

            }

            img {
                @include objectFit();
                position: absolute;
                top: 0;
                transition: all $speed $easing;
            }
        }

        &-meta {
            opacity: 0;
            overflow: visible;
            padding: 10px;
            position: absolute;
            bottom: 0;
            width: calc(100% - 20px);
            font-size: 12px;
            letter-spacing: .46px;
            transform: translatex(-30px);
            transition: all $speed $easing;
            z-index: 2;
            span {
                font-weight: bold;
                position: relative;
                text-transform: uppercase;
                z-index: 1;
            }
        }

        &-button {
            margin-top: 100px;

            &-float {
                position: relative;
                margin-top: 80px;

                .button {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    &.hide {
                        display: none;
                    }
                }
            }
        }

    }

    .displaying {
        font-size: 10px;
        text-transform: uppercase;
    }


    @include bp($h) {
        padding-bottom: 130px;

        .container {
            &-masonry {
                justify-content: space-between;
                margin-top: 65px;
            }
        }

        .wrapper {

            &-gallery-item {
                max-width: calc(50% - 8px);

                &:nth-child(odd) {
                    margin-right: 16px;
                }
                
            }

            &-button {
                margin-top: 100px;

                &-float {
                    position: relative;
                    margin-top: 80px;

                    .button {
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                    }
                }
            }

        }

    }
}